.navigation {
  height: 100%;
  width: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 15px 0 0 15px;
}

.navigation > h3 {
  font-weight: 800;
}

.navigation > span {
  text-transform: uppercase;
  margin: 10px;
  padding: 5px;
  font-weight: 600;
  cursor: pointer;

  border: 1px solid #000000;
  border-radius: 10px;
  background: #212121;
  color: white;
}

.navigation > span:hover {
  background: #f1f1f1;
  color: #212121;
}


