.zero-cross-game {
  margin: 0 auto;
  max-width: 1000px;
}

#field {
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 453px;
  height: 452px;
  border: 1px solid black;
  border-right: none;
}

#field > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 150px;
  height: 150px;
  font-size: 48px;
  cursor: pointer;
}

#field > div:nth-child(2n+1), #field > div:nth-child(2n+2) {
  border-bottom: 1px solid black;
  border-right: 1px solid black;
}

#field > .disabled.x-elem {
  background: green;
}

#field > .disabled.o-elem {
  background: red;
}

.zero-cross-game > button {
  text-transform: uppercase;
  margin: 10px;
  padding: 5px;
  font-weight: 600;
  cursor: pointer;

  border: 1px solid #000000;
  border-radius: 10px;
  background: #212121;
  color: white;
}

.zero-cross-game > button:hover {
  background: #f1f1f1;
  color: #212121;
}


