.App {
  text-align: center;
  margin: 0 auto;
  max-width: 1000px;

  font-family: Verdana;
}

.main {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.main > div:nth-child(1) {
  -ms-flex: 1 1;
      flex: 1 1;
}

.main > div:nth-child(2) {
  -ms-flex: 4 1;
      flex: 4 1;
  border-left: 1px solid black;
  border-right: 1px solid black;
  padding: 0 0 30px 10px;
}

.App-header, footer {
  background-color: #222;
  height: 50px;
  padding: 20px;
  color: white;
}

footer {
  background: black;
  color: white;
}



